import $ from "jquery";
import UAParser from "ua-parser-js";
import ColorThief from "colorthief";
import MobileDetect from "mobile-detect";
import bodyScrollLock from "body-scroll-lock";
import qrcode from "qrcode-generator";

function i18n() {
  function _typeof(obj) {
    if (typeof Symbol === "function" && typeof Symbol.iterator === "symbol") {
      _typeof = function _typeof(obj) {
        return typeof obj;
      };
    } else {
      _typeof = function _typeof(obj) {
        return obj &&
          typeof Symbol === "function" &&
          obj.constructor === Symbol &&
          obj !== Symbol.prototype
          ? "symbol"
          : typeof obj;
      };
    }
    return _typeof(obj);
  }

  function generateQR({ url, height, width }) {
    const typeNumber = 4;
    const errorCorrectionLevel = "Q";
    const qr = qrcode(typeNumber, errorCorrectionLevel);
    qr.addData(url);
    qr.make();
    document.getElementById("mobile-qr").innerHTML = qr.createImgTag(4);

    $("#mobile-qr img").css("height", height + "px");
  }

  // Accept #color and [r, g, b] color
  function pickTextColorBasedOnBgColorAdvanced(bgColor, lightColor, darkColor) {
    var r, g, b;

    if (bgColor.indexOf("#") !== -1) {
      var color = bgColor.charAt(0) === "#" ? bgColor.substring(1, 7) : bgColor;
      r = parseInt(color.substring(0, 2), 16); // hexToR

      g = parseInt(color.substring(2, 4), 16); // hexToG

      b = parseInt(color.substring(4, 6), 16); // hexToB
    } else {
      r = bgColor[0];
      g = bgColor[1];
      b = bgColor[2];
    }

    var uicolors = [r / 255, g / 255, b / 255];
    var c = uicolors.map(function (col) {
      if (col <= 0.03928) {
        return col / 12.92;
      }

      return Math.pow((col + 0.055) / 1.055, 2.4);
    });
    var L = 0.2126 * c[0] + 0.7152 * c[1] + 0.0722 * c[2];
    return L >= 0.175 ? darkColor : lightColor;
  }

  function luminanace(r, g, b) {
    var a = [r, g, b].map(function (v) {
      v /= 255;
      return v <= 0.03928 ? v / 12.92 : Math.pow((v + 0.055) / 1.055, 2.4);
    });
    return a[0] * 0.2126 + a[1] * 0.7152 + a[2] * 0.0722;
  }

  function langCode() {
    var currentLang = "";
    var langCode = "";

    if (navigator.languages !== undefined) {
      currentLang = navigator.languages[0];
    } else {
      currentLang = navigator.language;
    }

    if (currentLang.includes("zh")) {
      langCode = "zh";
    } else if (currentLang.includes("en")) {
      langCode = "en";
    } else {
      langCode = "vi"; // default language 'vi'
    }
    return langCode;
  }

  function renderLanguagePack(languagePack) {
    $("[data-lang-text]").each(function (index, value) {
      const key = $(value).data("lang-text");
      const currentLanguage = languagePack[key];
      $(value).text(currentLanguage);
    });
  }

  function contrast(rgb1, rgb2) {
    if (rgb1.indexOf("#") !== -1) {
      var r1, g1, b1;
      var c1 = rgb1.charAt(0) === "#" ? rgb1.substring(1, 7) : rgb1;
      r1 = parseInt(c1.substring(0, 2), 16); // hexToR

      g1 = parseInt(c1.substring(2, 4), 16); // hexToG

      b1 = parseInt(c1.substring(4, 6), 16); // hexToB
    } else {
      r1 = rgb1[0];
      g1 = rgb1[1];
      b1 = rgb1[2];
    }

    if (rgb2.indexOf("#") !== -1) {
      var r2, g2, b2;
      var c2 = rgb2.charAt(0) === "#" ? rgb2.substring(1, 7) : rgb2;
      r2 = parseInt(c2.substring(0, 2), 16); // hexToR

      g2 = parseInt(c2.substring(2, 4), 16); // hexToG

      b2 = parseInt(c2.substring(4, 6), 16); // hexToB
    } else {
      r2 = rgb2[0];
      g2 = rgb2[1];
      b2 = rgb2[2];
    }

    return (luminanace(r1, g1, b1) + 0.05) / (luminanace(r2, g2, b2) + 0.05);
  }

  function getTextColor(bgColor, lightColor, darkColor) {
    // console.log(contrast(lightColor, bgColor));
    // console.log(contrast(bgColor, darkColor));
    if (contrast(lightColor, bgColor) >= contrast(bgColor, darkColor)) {
      return lightColor;
    } else {
      return darkColor;
    }
  }

  function getUrlParameter(name) {
    name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
    var regex = new RegExp("[\\?&]" + name + "=([^&#]*)");
    var results = regex.exec(location.search);
    return results === null
      ? ""
      : decodeURIComponent(results[1].replace(/\+/g, " "));
  }

  function addToHomeScreen() {
    if (browser) {
      // $("html").html(browser);
      // return
    }

    var renderText = function () {
      if (browser.toLowerCase().indexOf("safari") !== -1) {
        return `
          <span data-lang-text="Click on the icon at the lower toolbar"></span> (<span class="a2hs--ios-share-icon"></span>)<br />
          <span data-lang-text="and choose 'Add to main screen"></span> (<span class="a2hs--plus-icon"></span>)
        `;
      } else if (browser.toLowerCase().indexOf("qqbrowser") !== -1) {
        return `
          <span data-lang-text="Click on the icon at the lower toolbar"></span> (<span class="a2hs--hamburger-icon"></span>), <span data-lang-text="choose add bookmark"></span><br />
          <span data-lang-text="then 'edit', and send to desktop"></span>
        `;
      } else if (browser.toLowerCase().indexOf("chrome") !== -1) {
        return `
          <span data-lang-text="Click on the icon at the upper toolbar"></span> (<span class='a2hs--dot-icon'></span>)<br />
          <span data-lang-text="and choose 'Add to main screen'"></span>
        `;
      } else if (browser.toLowerCase().indexOf("opera") !== -1) {
        return `
          <span data-lang-text="Click on the icon at the upper toolbar"></span> (<b>+</b>)<br />
          <span data-lang-text="and choose 'Add to main screen"></span>
        `;
      } else if (browser.toLowerCase().indexOf("ucbrowser") !== -1) {
        return `
           <span data-lang-text="Click on the icon at the lower toolbar"></span> (<span class="a2hs--hamburger-icon"></span>),<b><span data-lang-text="choose add bookmark"></span></b><br />
           <span data-lang-text="After saving bookmark successfully click the icon again"></span> (<span class="a2hs--hamburger-icon"></span>), <span data-lang-text="choose 'History and bookmarks management'"></span>,
           <span data-lang-text="long press the saved address and choose 'Send to mobile home screen'"></span>
        `;
      } else {
        return `
          <span data-lang-text="Click on the icon at the lower toolbar"></span> (<span class="a2hs--hamburger-icon"></span>)<br />
          <span data-lang-text="and choose 'Add to main screen"></span> (<span class="a2hs--plus-icon"></span>)
        `;
      }
    };

    var addToHomeScreenStyle = `
      <style>
        .a2hs--wrapper {
          position: fixed;
          width: 90%;
          bottom: 2%;
          padding: 12px 16px;
          box-sizing: border-box;
          border-radius: 6px;
          background-color: #f6f6f6;
          box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
          margin: auto;
          left: 0;
          right: 0;
        }

        .a2hs--title {
          margin: 0 0 4px;
          font-size: 14px;
        }

        .a2hs--close {
          position: absolute;
          top: 12px;
          right: 12px;
          height: 20px;
          width: 20px;
          font-size: 8px;
          line-height: 20px;
          border-radius: 50%;
          padding: 0;
          margin: 0;
          background-color: #666;
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
          border: 0;
          outline: none;
        }

        .a2hs--close svg {
          fill: #fff;
        }

        .a2hs--content-wrapper {
          display: flex;
          align-items: center;
        }

        .a2hs--content-wrapper .a2hs--icon {
          width: 50px;
          height: 50px;
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          z-index: 0;
          color: #fff;
          font-size: 17px;
          margin-right: 8px;
          background: url("data:image/png;base64,")
            center / contain no-repeat;
        }

        .a2hs--content-wrapper .a2hs--info {
          font-size: 12px;
          color: #838383;
        }

        .a2hs--ios-share-icon {
          background: url("data:image/png;base64,")
            center / contain no-repeat;
          width: 20px;
          height: 20px;
          display: inline-block;
          vertical-align: middle;
          margin: 0 4px 6px;
        }

        .a2hs--plus-icon {
          background: url("data:image/png;base64,")
            center / contain no-repeat;
          width: 20px;
          height: 20px;
          display: inline-block;
          vertical-align: middle;
          margin: 0 4px;
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
          border-radius: 4px;
        }
        
        .a2hs--hamburger-icon {
          background: url('data:image/png;base64,') center / contain no-repeat;
          height: 16px;
          width: 16px;
          display: inline-block;
          vertical-align: middle;
          margin: 8px 4px; 
          line-height: 20px;
        }
        
        .a2hs--dot-icon {
          background: url('data:image/png;base64,') center / contain no-repeat;
          height: 14px;
          width: 14px;
          display: inline-block;
          vertical-align: middle;
          margin: 6px 4px 8px; 
          line-height: 20px;
        }
      </style>
    `;

    var addToHomeScreenDOM = $(`
      <div class="a2hs--wrapper">
        <h1 class="a2hs--title" data-lang-text="Add shortcut to desktop"></h1>
        <button class="a2hs--close">
          <svg
            version="1.1"
            id="Capa_1"
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            x="0px"
            y="0px"
            width="1em"
            height="1em"
            viewBox="0 0 357 357"
            style="enable-background:new 0 0 357 357;"
            xml:space="preserve"
          >
            <g>
              <g id="close">
                <polygon
                  points="357,35.7 321.3,0 178.5,142.8 35.7,0 0,35.7 142.8,178.5 0,321.3 35.7,357 178.5,214.2 321.3,357 357,321.3 
				214.2,178.5 		"
                />
              </g>
            </g>
          </svg>
        </button>
        <div class="a2hs--content-wrapper">
          <div class="a2hs--icon"></div>
          <div class="a2hs--info">${renderText()}</div>
        </div>
      </div>
    `);

    if (isMobile.iOS()) {
      $("#android-manifest").remove();
    }

    $("head").append(addToHomeScreenStyle);
    $("body").append(addToHomeScreenDOM);
    $.ajax("./language/" + langCode() + ".json").done(function (languagePack) {
      renderLanguagePack(languagePack);
    });

    $(document).on("click", ".a2hs--close", function (e) {
      e.preventDefault();
      $(this).parents(".a2hs--wrapper").fadeOut();
    });
  }

  $(document).ready(function () {
    var browser = new UAParser().getBrowser().name;

    var isMobile = {
      Android: function () {
        return navigator.userAgent.match(/Android/i);
      },
      BlackBerry: function () {
        return navigator.userAgent.match(/BlackBerry/i);
      },
      iOS: function () {
        return navigator.userAgent.match(/iPhone|iPad|iPod/i);
      },
      Opera: function () {
        return navigator.userAgent.match(/Opera Mini/i);
      },
      Windows: function () {
        return (
          navigator.userAgent.match(/IEMobile/i) ||
          navigator.userAgent.match(/WPDesktop/i)
        );
      },
      any: function () {
        return (
          isMobile.Android() ||
          isMobile.BlackBerry() ||
          isMobile.iOS() ||
          isMobile.Opera() ||
          isMobile.Windows()
        );
      },
    };

    var imageDefer = $.Deferred();
    var dataDefer = $.Deferred();
    $.when(imageDefer, dataDefer).done(function (v1, v2) {
      $("body").addClass("ready");
    });
    $(".logo img").on("load", function () {
      var USE_COLOR_GENERATOR = true;

      if (USE_COLOR_GENERATOR) {
        var colorThief = new ColorThief();
        var palette = colorThief.getPalette($(".logo img")[0], 3, 12);
        var dominant = palette[0];
        var secondary = palette[1];
        var tertiary = palette[2];
        var lightColor = "#ffffff";
        var darkColor = "#666666";
        var dominantStyle = "\n        background-color: rgb("
          .concat(dominant[0], ",")
          .concat(dominant[1], ",")
          .concat(dominant[2], ");\n        color: ")
          .concat(getTextColor(dominant, lightColor, darkColor), "\n      ");
        var secondaryStyle = "\n        background-color: rgb("
          .concat(secondary[0], ",")
          .concat(secondary[1], ",")
          .concat(secondary[2], ");\n        color: ")
          .concat(getTextColor(secondary, lightColor, darkColor), "\n      ");
        var tertiaryStyle = "\n        background-color: rgb("
          .concat(tertiary[0], ",")
          .concat(tertiary[1], ",")
          .concat(tertiary[2], ");\n        color: ")
          .concat(getTextColor(tertiary, lightColor, darkColor), "\n      ");
        $(".nav").attr("style", dominantStyle);
        $(".instruction .footer").attr("style", secondaryStyle);
        $(".button-tutorial.ios, .button_download, .fm_icon").attr(
          "style",
          secondaryStyle
        );
        $(".instruction .footer .label").css(
          "color",
          getTextColor(secondary, lightColor, darkColor)
        );
        $(".instruction .footer .profile-download").attr(
          "style",
          dominantStyle
        );
        $(".button-tutorial.android").attr("style", tertiaryStyle);
      }

      imageDefer.resolve("image");
    });
    $(".logo img").attr("src", "img/logo512.png");

    $.ajax({
      // url: "data.json",
      url: "data.v2.json",
      error: function error(_error) {
        console.log(_error);
      },
      success: function success(data) {
        var data;

        if (_typeof(data) === "object") {
          data = data;
        } else {
          data = JSON.parse(data);
        }

        // var standalone = isMobile.iOS()
        //   ? "standalone" in window.navigator && window.navigator.standalone
        //   : window.matchMedia("(display-mode: standalone)").matches;

        var isInWebAppiOS = window.navigator.standalone == true;
        var isInWebAppChrome = getUrlParameter("mode") === "standalone";

        // var standalone = window.matchMedia("(display-mode: standalone)").matches

        var standalone = isMobile.iOS() ? isInWebAppiOS : isInWebAppChrome;

        var id = data.id;
        var app = data.app;
        var url = `https://api.airtable.com/v0/${app}/Franchise/${id}?api_key=keyKaV8UUKZnbOZ0X`;

        $.ajax({
          url: url,
          type: "GET",
          data: {},
          success: function (response) {
            // Get data from Airtable
            var data = response.fields;
            var appname = data.appname;
            var downloadDomain = data.urlDomain;
            var androidUrl = data.androidUrl;
            var iosUrl = data.iosUrl;
            var middleware = data.middleware;
            var superSign = data.superSign;
            var iosSuperSignUrl = data.iosSuperSignUrl;
            var iosEnterpriseSignUrl = data.iosEnterpriseSignUrl;
            var iosTestFlightUrl = data.iosTestFlightUrl;
            var cert = data.cert;
            var profileLink =
              `${downloadDomain}/profile/dummy.mobileprovision`;
            $("[data-appname-title]").text(
              "".concat(appname, "\u5E94\u7528\u4E0B\u8F7D")
            );
            $("[data-appname]").each(function () {
              $(this).text(appname).attr("data-appname", appname);
            });

            // default lang = vi
            var appCert1Text = `"Phân phối iPhone: ${cert}" chưa được tin cậy trên iPhone này. Cho đến khi nhà phát triển được tin cậy, các ứng dụng cấp doanh nghiệp của họ sẽ không khả dụng.`;
            var appCert3Text = `Ứng dụng từ nhà phát triển "Phân phối iPhone: ${cert}" không được tin cậy trên iPhone này và sẽ không chạy cho đến khi nhà phát triển được tin cậy.`;
            var appCert4Text = `Tin tưởng "${cert}"`;
            var appCert5Text = `Ứng dụng từ nhà phát triển "PHÂN PHỐI IPHONE: ${cert}"`;
            var appCert6Text = `Tin tưởng ứng dụng "Phân phối iPhone: ${cert}" trên iPhone này`;
            if (langCode() === "en") {
              appCert1Text = `"iPhone Distribution: ${cert}"Not yet trusted on this iPhone. Until the developer is trusted, its enterprise-level applications will be unavailable.`;
              appCert3Text = `The application from the developer "iPhone Distribution: ${cert}" has not been trusted on this iPhone and will not run until the developer is trusted.`;
              appCert4Text = `Trust "${cert}"`;
              appCert5Text = `Application from the developer "IPHONE DISTRIBUTION:${cert}"`;
              appCert6Text = `Trust the "iPhone Distribution:${cert}" application on this iPhone`;
            } else if (langCode() === "zh") {
              appCert1Text = `"iPhone Distribution: ${cert}"在此iPhone上尚未信任。在信任该开发者之前，其企业级应用将不可使用。`;
              appCert3Text = `来自开发者"iPhone Distribution: ${cert}"的应用在此iPhone上尚未信任，在信任此开发者之前将不会运行。`;
              appCert4Text = `信任"${cert}"`;
              appCert5Text = `来自开发者"IPHONE DISTRIBUTION:${cert}"的应用`;
              appCert6Text = `在此iPhone上信任"iPhone Distribution:${cert}"应用`;
            }

            if (data.cert) {
              $(".popup [data-cert]").each(function () {
                if ($(this).hasClass("app-cert-1")) {
                  $(this).text(appCert1Text);
                } else if ($(this).hasClass("app-cert-2")) {
                  $(this).text(cert);
                } else if ($(this).hasClass("app-cert-3")) {
                  $(this).text(appCert3Text);
                } else if ($(this).hasClass("app-cert-4")) {
                  $(this).text(appCert4Text);
                } else if ($(this).hasClass("app-cert-5")) {
                  $(this).text(appCert5Text);
                } else if ($(this).hasClass("app-cert-6")) {
                  $(this).text(appCert6Text);
                }
              });

              $(".content.ios [data-cert]").each(function () {
                if ($(this).hasClass("app-cert-1")) {
                  $(this).text(appCert1Text);
                } else if ($(this).hasClass("app-cert-2")) {
                  $(this).text(cert);
                } else if ($(this).hasClass("app-cert-3")) {
                  $(this).text(appCert3Text);
                } else if ($(this).hasClass("app-cert-4")) {
                  $(this).text(appCert4Text);
                } else if ($(this).hasClass("app-cert-5")) {
                  $(this).text(appCert5Text);
                } else if ($(this).hasClass("app-cert-6")) {
                  $(this).text(appCert6Text);
                }
              });
            } else {
              $("[data-cert-image]").each(function () {
                $(this).attr(
                  "src",
                  "img/" + langCode() + "/" + $(this).data("alt-image")
                );
              });
            }

            $("#ios .button_download").attr(
              "href",
              "itms-services://?action=download-manifest&amp;url=".concat(
                iosUrl
              )
            );
            $("#android .button_download").attr("href", androidUrl);

            const targetElement = document.querySelector(
              "#instruction .content"
            );
            $(".button_download").click(function (e) {
              bodyScrollLock.disableBodyScroll(targetElement);
              setTimeout(function () {
                $("#instruction").addClass("show");
              }, 200);
              $("#instruction-overlay").addClass("show");
            });
            $(".instruction .close-instruction").click(function (e) {
              e.preventDefault();
              bodyScrollLock.enableBodyScroll(targetElement);
              $("#instruction").removeClass("show");
              setTimeout(function () {
                $("#instruction-overlay").removeClass("show");
              }, 200);
            });
            $(".instruction .profile-download").attr("href", profileLink); // Render QR Code

            var currentType;
            var md = new MobileDetect(window.navigator.userAgent);

            if (md.is("ios") === true) {
              $("#ios").show();
              currentType = "ios";

              if (superSign && !(iosEnterpriseSignUrl || iosTestFlightUrl)) {
                location.href = iosSuperSignUrl;
              } else {
                /**
                 * Enterprise Sign or Test Flight Sign
                 */
                let urls = [];
                const clonedButton = $("#ios a").clone();
                const originalButtonText = clonedButton.text();

                $("#ios a")
                  .attr("href", iosSuperSignUrl)
                  .text(originalButtonText + " #1");

                if (iosTestFlightUrl) {
                  urls.push(iosTestFlightUrl);
                }

                if (iosEnterpriseSignUrl) {
                  urls.push(iosEnterpriseSignUrl);
                }

                urls.forEach(function (url, index) {
                  const number = urls.length + 1 - index;
                  const extraButton = clonedButton
                    .clone()
                    .attr("href", url)
                    .css("margin", "10px auto")
                    .text(originalButtonText + " #" + number);
                  $("#ios a:first-child").after(extraButton);
                });

                $("#ios").show();
              }
            } else if (md.is("AndroidOS") === true) {
              $("#android").show();
              currentType = "android";
            } else {
              var api = "https://".concat(
                middleware
                  ? middleware.indexOf("://") !== -1
                    ? middleware.split("://").slice(-1)
                    : middleware
                  : "njstzapp.com",
                "/active"
              );
              var currentProtocol = location.protocol;
              var currentDomain = location.hostname;
              var oldUrl = currentProtocol + "//" + currentDomain;
              var shortUrl = oldUrl ? oldUrl.split("://").slice(-1) : oldUrl;
              $.ajax({
                url: api,
                success: function success(data) {
                  var newUrl = "http://" + data + "?r=" + btoa(shortUrl) + "g";
                  generateQR({
                    url: newUrl,
                    width: 180,
                    height: 180,
                  });
                },
                error: function error(_error2) {
                  console.log(_error2);
                  generateQR({
                    url: oldUrl,
                    width: 180,
                    height: 180,
                  });
                },
              });
              $("#current-domain").text(currentDomain);
              $("#otherdevice").show();
            } // Remove and hide based on platform type

            $("[data-show-with]").each(function (i, elem) {
              var typeFilter = $(elem).attr("data-show-with");

              switch (currentType) {
                case "ios":
                  if (typeFilter.indexOf("ios") === -1) $(elem).remove();
                  break;

                case "android":
                  if (typeFilter.indexOf("android") === -1) $(elem).remove();
                  break;
              }
            });

            $("head").append(
              `<meta name="apple-mobile-web-app-title" content="${appname}">`
            );

            // Add to home screen is not allowed in latest version of Chrome iOS
            if (!standalone && (isMobile.iOS() || isMobile.Android())) {
              if (
                !(
                  isMobile.iOS() &&
                  browser.toLowerCase().indexOf("chrome") !== -1
                )
              ) {
                addToHomeScreen();
              }
            }
          },
          error: function () {},
        });
      },
    }).done(function () {
      dataDefer.resolve("data");
    });

    // load image according to language
    $("[data-src]").each(function () {
      $(this).attr("src", "img/" + langCode() + "/" + $(this).data("src"));
    });

    $.ajax("./language/" + langCode() + ".json").done(function (languagePack) {
      renderLanguagePack(languagePack);
    });
  });

  console.log("i18nSkript");
}

export default i18n;
